.cardSty {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  flex-wrap: wrap;
background-color: rgba(255, 255, 255, 0);
}
.cardSty .card {
  position: relative;
  width: 280px;
  height: 500px;
  margin: 20px;
  box-shadow: 20px 20px 50px rgb(0, 0, 0, 0.5);
  background-color: #ffffff01;
  border-radius: 15px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}
.cardSty .card .contents {
  padding: 10px;
  text-align: center;
  transition: 0.5s;
  color: rgba(255, 255, 255, 0.5);
}

.cardSty .card .hoverContent .fevrate:active,
.cardSty .card .hoverContent .fevrate:hover,
.cardSty .card .hoverContent .fevrate:focus{
  color: rgb(245, 220, 0);
}

.cardSty .card .contents  .imgfluid {
  width: 150px;
  height: 150px;
  border-radius: 50%;
}
.cardSty .card .contents .title-1 {
  color: #fff;
  margin-top: 20px;
  font-weight: 600;
  font-family: "PT Serif", serif;
  font-size: 20px;
}
.cardSty .card .contents .title-2 {
  color: rgb(255, 255, 255, 0.9);
  font-weight: 500;
  font-size: 18px;
}
.cardSty .card .hoverContent {
  position: absolute;
  left: 10px;
  bottom: 0px;
  right: 10px;
  color: #fff;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  transition: -webkit-transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
  overflow: auto;
  max-height: 100%;
  font-weight: 400;
  top: 0px;
}
.cardSty .card .hoverContent .contentitem{
  margin-top: 20px;
  
}

.cardSty .card .hoverContent .contentitem p span{
    font-weight: 900;
    font-size: 24px;
    padding-top: 10px;
}
.cardSty .card .hoverContent .imgfluid{
  height: auto;
  width: 280px;
  
}
.cardSty .card:hover .hoverContent{
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    transition: -webkit-transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
    cursor: pointer;
}
.cardSty .card:hover .contents{
 
  opacity: 0;
}
@media screen and (max-width: 600px) {
  .cardSty .card {
    width: 80vw;
  }
  
}
